<template>
  <section>
    <v-container>
      <v-row>
        <v-col cols="12" md="2">
          <Multiselect
            :options="optionsarea"
            :searchable="true"
            :placeholder="$t('Area')"
            label="name"
            class="selectinput"
            v-model="area_id"
            @change="handlePageChange('','','','',area_id,param)"
          />
        </v-col>
        <v-col cols="12" md="2">
          <multiselect
            :options="optionscity"
            :searchable="true"
            :placeholder="$t('City')"
            label="name"
            class="selectinput"
            v-model="city_id"
            @change="handlePageChange('','',city_id,'','',param)"

          ></multiselect>
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field
            class="position-relative custominput"
            density="compact"
            :placeholder="$t('Placesearch')"
            single-line
            hide-details
            elevation="0"
            v-model="search"
            @change="handlePageChange('','','',search,'',param)"
            type="text"
          >
            <div
              class="position-absolute d-flex justify-space-between align-center"
              style="width: 96% !important"
            >
              <v-icon icon="mdi-magnify"> </v-icon>
              <v-icon icon="mdi-filter-outline"></v-icon>
            </div>
          </v-text-field>
        </v-col>
        <v-col cols="12" md="2">
          <v-btn class="previewads" elevation="0">
            {{ $t("Searchnow") }}
            <v-icon class="arrow" icon="mdi-chevron-left"> </v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-container>

    <v-list class="d-flex align-center listtype">
      <v-list-item
        color="deep-purple-accent-4"
        v-for="(list1, ii) in list"
        :key="ii"
        class="itemname pa-0"
        :class="ii === selected && 'active'"

        @click="
          handlePageChange(
            (ad_type = list1.ad_type_id),
            (estate_type = list1.estate_type_id),city_id,search,area_id,param
          )
        "
      >
        <p style="padding: 4px 16px" @click="onClickDescription(ii)">
          {{ list1.name }}
        </p>
      </v-list-item>
    </v-list>
    <v-container>
      <v-row class="justify-center">
        <v-col cols="12" md="7">
          <div class="collection-real">
            <button
              class="button-real mx-1"
              :class="{ active: ff.active }"
              @click="
          handlePageChange(
            estate_type,ad_type,city_id,search,area_id,ff.text
          )
        "
           
              v-for="ff in itembutton"
              :key="ff.id"
            >
              <p @click="active()">
                {{ $t(ff.title) }}
              </p>
            </button>
          </div>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" md="4" v-for="(ad, i) in items" :key="i">
          <Card
            :image="ad.attachment[0]"
            :title="ad.price"
            :subtitle="ad.estate_type + ' ' + ad.ad_type"
            :city="ad.city"
            :date="ad.publish_date"
            :type="ad.estate_type"
            :idcard="ad.id"
          ></Card>
          <!-- :numwidth="ad.area.split('.')[0]" -->
        </v-col>
      </v-row>
      <v-pagination
        class="paginate"
        v-model="page"
        :length="pagination.last_page"
        :total-visible="5"
        @update:model-value="handlePageChange()"
        v-if="items.length > 0"
      ></v-pagination>
    </v-container>
  </section>
</template>

<script>
import Multiselect from "@vueform/multiselect";

import Card from "../Card/Card.vue";
import crudDataService from "@/Services/crudDataService";
export default {
  components: {
    Multiselect,
    Card,
  },
  data() {
    return {
      optionscity: [],
      optionsarea: [],
      items: [],
      pagination: [],
      options: [],
      page: 1,
      list: [],
      ad_type: "",
      estate_type: "",
      param: "sell",
      selected: null,
      itembutton: [
        { title: "Real_offers", id: 1, text: "sell", active: true },
        { title: "Real_requests", id: 2, text: "buy", active: false },
      ],
      city_id: "",
      area_id: "",
      search: "",
    };
  },
  watch: {
    // "area_id"(newValue) {
    //  console.log("wojjowwpojwpowoojw",newValue);
    //  this.area_new=newValue
    // },
    // "city_id"(newValue) {
    //  console.log(newValue);
    //  this.city_new=newValue
    // },
  },
  methods: {
    async Areas() {
      let response = await crudDataService.getAll(`areas`);
      this.optionsarea = response.data.data.map((state) => ({
        value: state.id,
        name: state.name,
      }));
    },
    async City() {
      let response = await crudDataService.getAll(`cities`);
      this.optionscity = response.data.data.map((state) => ({
        value: state.id,
        name: state.name,
      }));
    },
    active() {
      this.itembutton.map((button, index) => {
        button.active = !button.active;
      });
    },

    onClickDescription(i) {
      this.selected = i;
    },
    async handlePageChange(
      estate_type,
      ad_type,
      city_id,
      search,
      area_id,
      param
    ) {
      // sell?perPage=6&estate_type_id=&city_id=&ad_type_id=&page=1&area_id=&search=	200	xhr	xhr.js:256	4.0 kB	1.72 s	types	200	xhr	xhr.js:256	731 B	1.40 s	areas	200	xhr	xhr.js:256	491 B	1.49 s	cities	200	xhr	xhr.js:256	5.0 kB	1.32 s	sell?perPage=6&estate_type_id=&city_id=&ad_type_id=&page=1&area_id=&search=	200	xhr	xhr.js:256	4.0 kB	1.84 s	sell?perPage=6&estate_type_id=&city_id=&ad_type_id=&page=1&area_id=&search=	200	xhr	xhr.js:256	4.0 kB	1.70 s						

      (this.estate_type = estate_type||`${this.estate_type}`),
        (this.ad_type = ad_type||`${this.ad_type}`),
        (this.param = param||`${this.param}`),
        (this.city_id = city_id||`${this.city_id}`),
        (this.area_id = area_id||`${this.area_id}`);
        // (this.search = search);
      let response = await crudDataService.filter(`ads`, param||`${this.param}`, {
        perPage: 6,
        estate_type_id: estate_type || ``,
        city_id: this.city_id || ``,
        ad_type_id: ad_type || ``,
        page: `${this.page}` || ``,
        area_id: this.area_id || ``,
        search: `${this.search}` || ``,
      });
      console.log("estate_type", estate_type);
      console.log("adtype", ad_type);
      console.log("param", param);
      this.items = response.data.data.ads;
      console.log(this.items);
      console.log(this.items);
      this.pagination = response.data.data.meta;
      this.items.forEach((element) => {
        this.options = element.options;
      });
    },

    async alltypes() {
      let response = await crudDataService.getAll(`types`);
      this.list = response.data.data;
    },
  },

  mounted() {
    this.handlePageChange();
    this.alltypes();
    this.Areas();
    this.City();
  },
};
</script>
<style lang="scss">
.collection-real {
  background: #f7f7f7;
  padding: 10px;
  border-radius: 8px;
  .button-real {
    padding: 5px 15px;
    border-radius: 8px;
    color: #000000;
    font-weight: 500;
    font-size: 16px;
    width: calc(97% / 2);
    &.active {
      background: #f6c712;
    }
  }
}
.paginate {
  margin-top: 15px;
  .v-btn--icon .v-icon {
    transform: rotateY(180deg);
  }
}
[dir="ltr"] {
  .paginate .v-btn--icon .v-icon {
    transform: none;
  }
}
.itemname {
  border-radius: 12px;
  margin: 0 10px 5px;
  background: #f9f9f9;
  &.active {
    border: 1px solid #f6c712;
    box-shadow: 0px 3px 8px 0px #6b6b6b2e;
  }
}

.listtype::-webkit-scrollbar {
  height: 0 !important;
}
.custominput {
  &.v-text-field .v-field__input {
    align-items: center;
    input {
      margin-right: 30px;
      margin-left: 30px;
    }
  }
  .v-field--variant-filled .v-field__outline::before,
  .v-field--variant-underlined .v-field__outline::before {
    border-style: none;
  }
}
.selectinput {
  &.multiselect {
    border: none !important;
    height: 100%;
  }
  .multiselect-search {
    background: #d9d9d938 !important;
  }
}
</style>
<style lang="scss" scoped>
.previewads {
  width: 100%;
  height: 45px;
  border-radius: 4px;
}
</style>
