<template>
  <v-footer class="footer">
    <v-container>
      <div
        class="d-flex flex-sm-row flex-wrap flex-column text-center justify-sm-center justify-start mb-5"
      >
        <div
          v-for="link in links"
          :key="link.text"
          class="font-weight-bold text-sm-cnter text-start"
        >
          <p class="px-2 pb-2" @click="navigateTo(link.route)">
            {{ $t(link.text) }}
          </p>
        </div>
      </div>
      <v-row class="text-center">
        <v-col cols="12">
          <v-row justify="center">
            <div class="mx-5">
              <a
                :href="`${items.twitter}`"
                target="_blank"
                class="text-white text-decoration-none"
              >
                <v-img :src="twitter" class="sochial"></v-img>
              </a>
            </div>
            <div class="mx-5">
              <a
                :href="`${items.instagram}`"
                target="_blank"
                class="text-white text-decoration-none"
              >
                <v-img :src="insta" class="sochial"></v-img>
              </a>
            </div>
            <div class="mx-5">
              <a
                :href="`${items.facebook}`"
                target="_blank"
                class="text-white text-decoration-none"
              >
                <v-img :src="face" class="sochial"></v-img>
              </a>
            </div>
          </v-row>
        </v-col>
        <v-col cols="12" class="font-weight-bold">
          {{ $t(footerText) }}
          @2023
        </v-col>
      </v-row>
    </v-container>
  </v-footer>
</template>

<script>
import crudDataService from "@/Services/crudDataService";
import insta from "@/assets/icon/insta.png";
import face from "@/assets/icon/face.png";
import twitter from "@/assets/icon/twitter.png";
export default {
  data() {
    return {
      links: [
        { text: "Partner", route: "/partner" },
        { text: "privacy", route: "/privacy" },
        { text: "Blog", route: "/blog" },
        { text: "About", route: "/about-us" },
        { text: "Contact", route: "/contact" },
      ],
      twitter: twitter,
      insta: insta,
      face: face,

      footerText: "all_rights",
      items: "",
    };
  },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
    },
    async getcontact() {
    let response=  await crudDataService.getAll("application-data");
      this.items = response.data.data;
    },
  },
  mounted() {
    this.getcontact();
  },
};
</script>
<style lang="scss" scoped>
.footer {
  background: #1f2937;
  color: white;
  cursor: pointer;
  .sochial {
    width: 24px;
  }
}
</style>
