<template>
  <section v-if="items">
    <h3 class="text-h4 font-weight-bold text-center mb-8">
      {{ $t("Contact") }}
    </h3>
    <v-row>
      <v-col cols="12" sm="4" class="overflow-hidden">
        <img :src="Path" alt="" class="img-style" style="width: 300px; height: 200px" />
        <v-img
          :src="contactus"
          alt=""
          cover
          class="rounded position-relative style_mon"
          style="z-index: 110; top: -42%; right: 3%"
        >
          <div class="pa-5 rounded h-100" style="background: #00000033">
            <p class="text-white text-md-h5 text-h6 mb-4">
              {{ $t("contact_information") }}
            </p>
            <a
              :href="`tel:+${items.phone}`"
              class="text-white text-decoration-none d-block mb-3"
            >
              <v-icon icon="mdi-phone-outline"></v-icon>
              {{ items.phone }}
            </a>
            <a
              :href="`mailto:${items.email}`"
              class="text-white text-decoration-none d-block mb-3"
            >
              <v-icon icon="mdi-email-outline"></v-icon>
              {{ items.email }}
            </a>
            <a
              :href="`https://api.whatsapp.com/send?phone=${items.whatsapp}`"
              target="_blank"
              class="text-white text-decoration-none d-block mb-3"
            >
              <v-icon icon="mdi-whatsapp" class="mx-1"></v-icon>
              {{ items.whatsapp }}
            </a>
            <div class="d-flex justify-space-between w-75">
              <a
                :href="`${items.facebook}`"
                target="_blank"
                class="text-white text-decoration-none"
              >
                <v-icon icon="mdi-facebook" class="mx-1"></v-icon>
              </a>
              <a
                :href="`${items.instagram}`"
                target="_blank"
                class="text-white text-decoration-none"
              >
                <v-icon icon="mdi-instagram" class="mx-1"></v-icon>
              </a>
              <a
                :href="`${items.linkedin}`"
                target="_blank"
                class="text-white text-decoration-none"
              >
                <v-icon icon="mdi-linkedin" class="mx-1"></v-icon>
              </a>
              <a
                :href="`${items.snapchat}`"
                target="_blank"
                class="text-white text-decoration-none"
              >
                <v-icon icon="mdi-snapchat" class="mx-1"></v-icon>
              </a>
            </div>
          </div>
        </v-img>
      </v-col>
      <v-col cols="12" sm="8">
        <div class="mt-sm-1 mt-2">
            <label class="font-weight-bold">
      {{ $t("message") }}
    </label>
    <v-textarea 
    variant="solo"
      class="selectnew"
      v-model="message"
    ></v-textarea>
  
    <label class="font-weight-bold">
      {{ $t("phone_number") }}
    </label>
    <v-text-field
    type="tel"
      variant="solo"
      class="selectnew tel"
      v-model="phone"
      @input="validatePhoneNumber" 

    ></v-text-field>
    <div v-if="invalidPhoneNumber" class="text-red mb-2">
      {{ validationMessage }}
    </div>
    <div class="text-red">{{ errorMessage ? errorMessage : "" }}</div>

    <button class="pa-2 previewads w-100 mt-4" @click="contact">
        {{ $t('Send') }}
    </button>
        </div>
     
      </v-col>
    </v-row>
  </section>
  <v-row v-else>
    <v-col cols="12" v-for="text in texts" :key="text">
      <v-skeleton-loader
        class="mx-auto border-0"
        type="paragraph"
      ></v-skeleton-loader>
    </v-col>
  </v-row>
</template>
<script>
import crudDataService from "@/Services/crudDataService";
import contact from "../../assets/images/contactus.png";
import Path from "../../assets/images/Path.png";
export default {
  data() {
    return {
      items: "",
      contactus: contact,
      Path: Path,
      texts: [1, 2, 3, 4],
      phone:"",
      message:"",
      errorMessage: "",
      invalidPhoneNumber:false,
      validationMessage:"",


    };
  },
  methods: {
    validatePhoneNumber() {
      const isNegative = this.phone.startsWith('-');
      const isNumeric = /^\d+$/.test(this.phone);
      this.invalidPhoneNumber = isNegative || !isNumeric;
         this.validationMessage = this.$t('enterValidPhoneNumber');
    },
    async getcontact() {
      let response = await crudDataService.getAll("application-data");
      this.items = response.data.data;
    },
    async contact() {
      let response = await crudDataService.create("contact-us",{
        message:this.message,
        phone:this.phone
      })
      .then((res) => {
          console.log(res.data.data.token, "wdjqpowjpwd");
       
        })
        .catch((error) => {
            this.errorMessage = error.response.data.message;
        });
    },
  },
  mounted() {
    this.getcontact();
  },
};
</script>
<style lang="scss">
@media (max-width:550px) {
    .img-style{
        display: none;
    }
    .style_mon{
        right: auto !important;
        top: auto !important;
    }
}
.tel input {
    text-align: end !important;
}
</style>
